import React, { useState } from 'react';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { getFirestore, collection, addDoc } from 'firebase/firestore';
import { auth, db } from './firebaseConfig';
import 'bootstrap/dist/css/bootstrap.min.css';

const Registro = ({ handleMostrarInicioSesion }) => {
  const [nombre, setNombre] = useState('');
  const [correo, setCorreo] = useState('');
  const [contrasena, setContrasena] = useState('');
  const [confirmarContrasena, setConfirmarContrasena] = useState('');
  const [mostrarContrasena, setMostrarContrasena] = useState(false);
  const [nivelContrasena, setNivelContrasena] = useState(null);
  const [mensaje, setMensaje] = useState(null);

  const handleRegistro = (event) => {
    event.preventDefault();
    if (contrasena === confirmarContrasena) {
      createUserWithEmailAndPassword(auth, correo, contrasena)
        .then((userCredential) => {
          const user = userCredential.user;
          const usuariosCollection = collection(db, 'usuarios');
          addDoc(usuariosCollection, {
            nombre: nombre,
            correo: correo,
          })
            .then((docRef) => {
              setMensaje('Registro exitoso. Redirigiendo a inicio de sesin...');
              setTimeout(() => {
                handleMostrarInicioSesion();
                setNombre('');
                setCorreo('');
                setContrasena('');
                setConfirmarContrasena('');
              }, 2000);
            })
            .catch((error) => {
              setMensaje('Error al registrar usuario');
            });
        })
        .catch((error) => {
          if (error.code === 'auth/email-already-in-use') {
            setMensaje('Correo electrnico ya registrado');
          } else {
            setMensaje('Error al registrar usuario');
          }
        });
    } else {
      setMensaje('Las contraseas no coinciden');
    }
  };

  const validarContrasena = (contrasena) => {
    let nivel = null;
    if (contrasena.length < 8) {
      nivel = 'Dbil';
    } else if (contrasena.length >= 8 && contrasena.length < 12) {
      nivel = 'Moderado';
    } else {
      nivel = 'Fuerte';
    }
    setNivelContrasena(nivel);
  };

  const handleContrasenaChange = (event) => {
    setContrasena(event.target.value);
    validarContrasena(event.target.value);
  };

  const handleMostrarContrasena = () => {
    setMostrarContrasena(!mostrarContrasena);
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-4">
          <div className="card shadow p-3 mb-5 bg-white rounded">
            <div className="card-body">
              <h2 className="text-center">Registro</h2>
              <div className="text-center mb-3">
              <img src="logot.svg" alt="Logo" width="100" height="100" />
              </div>
              <form onSubmit={handleRegistro}>
                <div className="form-group mb-3">
                  <label>Nombre</label>
                  <input
                    type="text"
                    className="form-control"
                    value={nombre}
                    onChange={(event) => setNombre(event.target.value)}
                  />
                </div>
                <div className="form-group mb-3">
                  <label>Correo electrnico</label>
                  <input
                    type="email"
                    className="form-control"
                    value={correo}
                    onChange={(event) => setCorreo(event.target.value)}
                  />
                </div>
                <div className="form-group mb-3">
                  <label>Contrasea</label>
                  <div className="input-group">
                    <input
                      type={mostrarContrasena ? 'text' : 'password'}
                      className="form-control"
                      value={contrasena}
                      onChange={handleContrasenaChange}
                    />
                    <div className="input-group-append">
                      <button
                        type="button"
                        className="btn btn-link"
                        onClick={handleMostrarContrasena}
                      >
                        <i className={mostrarContrasena ? 'fas fa-eye-slash' : 'fas fa-eye'} />
                      </button>
                    </div>
                  </div>
                </div>
                <div className="form-group mb-3">
                  <label>Confirmar contrasea</label>
                  <input
                    type={mostrarContrasena ? 'text' : 'password'}
                    className="form-control"
                    value={confirmarContrasena}
                    onChange={(event) => setConfirmarContrasena(event.target.value)}
                  />
                </div>
                {nivelContrasena && (
                  <p>
                    Nivel de contrasea: <strong>{nivelContrasena}</strong>
                  </p>
                )}
                {mensaje && (
                  <p>
                    <strong>{mensaje}</strong>
                  </p>
                )}
                <button type="submit" className="btn btn-primary w-100 mb-3">
                  Registrarse
                </button>
                <p className="text-center">
                  Ya tienes una cuenta?{' '}
                  <a href="#" onClick={handleMostrarInicioSesion}>
                    Iniciar sesin
                  </a>
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Registro;