import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import InicioSesion from './InicioSesion';
import Registro from './Registro';
import RegistroSalidaRetorno from './RegistroSalidaRetorno';
import FuelRequest from './FuelRequest';
import ReportUnit from './ReportUnit';

function App() {
  const [mostrarInicioSesion, setMostrarInicioSesion] = useState(true);
  const [mostrarMenu, setMostrarMenu] = useState(false);
  const [usuario, setUsuario] = useState(null);
  const [tabs, setTabs] = useState([]);
  const [activeTab, setActiveTab] = useState(null);

  const handleMostrarRegistro = () => {
    setMostrarInicioSesion(false);
  };

  const handleMostrarInicioSesion = () => {
    setMostrarInicioSesion(true);
  };

  const handleInicioSesion = (usuario) => {
    setMostrarMenu(true);
    setMostrarInicioSesion(false);
    setUsuario(usuario);
  };

  const handleCerrarSesion = () => {
    setMostrarMenu(false);
    setMostrarInicioSesion(true);
    setUsuario(null);
  };

  const handleOpenTab = (tab) => {
    if (!tabs.includes(tab)) {
      setTabs([...tabs, tab]);
    }
    setActiveTab(tab);
  };

  const handleCloseTab = (tab) => {
    setTabs(tabs.filter((t) => t !== tab));
    if (tab === activeTab) {
      setActiveTab(tabs.length > 1 ? tabs[0] : null);
    }
  };

  return (
    <div className="container-fluid">

<nav className="navbar navbar-expand-sm navbar-light bg-light fixed-top">
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto flex-column">
      {mostrarMenu && (
        <li className="nav-item">
          <a className="nav-link" href="#" onClick={() => handleOpenTab("registro-salida-retorno")}>
            Registro Salida Retorno
          </a>
        </li>
      )}
      {mostrarMenu && (
        <li className="nav-item">
          <a className="nav-link" href="#" onClick={() => handleOpenTab("solicitud-combustible")}>
            Solicitud de Combustible
          </a>
        </li>
      )}
      {mostrarMenu && (
        <li className="nav-item">
          <a className="nav-link" href="#" onClick={() => handleOpenTab("reportar-fallas-unidad")}>
            Reportar Fallas en Unidad
          </a>
        </li>
      )}
      {mostrarMenu && (
        <li className="nav-item">
          <a className="nav-link" href="#" onClick={handleCerrarSesion}>
            Cerrar Sesin
          </a>
        </li>
      )}
    </ul>
  </div>
</nav>


      <div className="row">
        {/* Contenido principal */}
        <main role="main" className="col-md-9 ml-sm-auto col-lg-10 px-4">
          {mostrarInicioSesion && (
            <InicioSesion
              handleMostrarRegistro={handleMostrarRegistro}
              handleInicioSesion={handleInicioSesion}
            />
          )}
          {!mostrarInicioSesion && !mostrarMenu && (
            <Registro handleMostrarInicioSesion={handleMostrarInicioSesion} />
          )}
          {mostrarMenu && (
            <div>
              
              <h1 className="my-4">Gestor de Rutas</h1>
              <div className="tab-content">
                {tabs.map((tab, index) => (
                  <div key={index} className={tab === activeTab ? 'tab-pane active' : 'tab-pane'}>
                    {tab === 'registro-salida-retorno' && <RegistroSalidaRetorno />}
                    {tab === 'solicitud-combustible' && <FuelRequest />}
                    {tab === 'reportar-fallas-unidad' && <ReportUnit />}
                  </div>
                ))}
              </div>
            </div>
          )}
        </main>
      </div>
    </div>
  );
}

export default App;