import React, { useState } from 'react';
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';
import { auth } from './firebaseConfig';
import 'bootstrap/dist/css/bootstrap.min.css';

const InicioSesion = ({ handleMostrarRegistro, handleInicioSesion }) => {
  const [correo, setCorreo] = useState('');
  const [contrasena, setContrasena] = useState('');
  const [mostrarContrasena, setMostrarContrasena] = useState(false);

  const handleInicioSesionFirebase = (event) => {
    event.preventDefault();
    signInWithEmailAndPassword(auth, correo, contrasena)
      .then((userCredential) => {
        handleInicioSesion({ nombre: 'Usuario' });
      })
      .catch((error) => {
        console.error(error);
      });
  };

  const handleMostrarContrasena = () => {
    setMostrarContrasena(!mostrarContrasena);
  };

  return (
    <div className="container mt-5">
      <div className="row justify-content-center">
        <div className="col-md-4">
          <div className="card shadow p-3 mb-5 bg-white rounded">
            <div className="card-body">
              <div className="text-center mb-3">
                <img src="logot.svg" alt="Logo" width="100" height="100" />
              </div>
              <h2 className="text-center">Iniciar sesin</h2>
              <form onSubmit={handleInicioSesionFirebase}>
                <div className="form-group mb-3">
                  <label>Correo electrnico</label>
                  <input
                    type="email"
                    className="form-control"
                    value={correo}
                    onChange={(event) => setCorreo(event.target.value)}
                  />
                </div>
                <div className="form-group mb-3">
                  <label>Contrasea</label>
                  <div className="input-group">
                    <input
                      type={mostrarContrasena ? 'text' : 'password'}
                      className="form-control"
                      value={contrasena}
                      onChange={(event) => setContrasena(event.target.value)}
                    />
                    <div className="input-group-append">
                      <button
                        type="button"
                        className="btn btn-link"
                        onClick={handleMostrarContrasena}
                      >
                        <i className={mostrarContrasena ? 'fas fa-eye-slash' : 'fas fa-eye'} />
                      </button>
                    </div>
                  </div>
                </div>
                <button type="submit" className="btn btn-primary w-100 mb-3">
                  Iniciar sesin
                </button>
                <p className="text-center">
                  No tienes una cuenta?{' '}
                  <a href="#" onClick={handleMostrarRegistro}>
                    Regstrate
                  </a>
                </p>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default InicioSesion;