import React, { useState, useEffect } from 'react';
import { getFirestore, collection, getDocs, addDoc, updateDoc, doc, onSnapshot } from 'firebase/firestore';
import { db } from './firebaseConfig';
import { getAuth, onAuthStateChanged } from 'firebase/auth';

const auth = getAuth();

const RegistroSalidaRetorno = () => {
  const [numeroUnidad, setNumeroUnidad] = useState('');
  const [placa, setPlaca] = useState('');
  const [tipoUnidad, setTipoUnidad] = useState('');
  const [kilometroSalida, setKilometroSalida] = useState('');
  const [ruta, setRuta] = useState('');
  const [kilometroRetorno, setKilometroRetorno] = useState('');
  const [unidades, setUnidades] = useState([]);
  const [pilotos, setPilotos] = useState([]);
  const [auxiliares, setAuxiliares] = useState([]);
  const [pilotoSeleccionado, setPilotoSeleccionado] = useState('');
  const [auxiliarSeleccionado, setAuxiliarSeleccionado] = useState('');
  const [salidas, setSalidas] = useState([]);
  const [salidasCompletas, setSalidasCompletas] = useState([]);
  const [userId, setUserId] = useState('');

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setUserId(user.uid);
      } else {
        setUserId('');
      }
    });
  }, []);

  useEffect(() => {
    const obtenerUnidades = async () => {
      const unidadesRef = collection(db, 'units');
      const unidadesSnap = await getDocs(unidadesRef);
      const unidadesData = unidadesSnap.docs.map((doc) => doc.data());
      setUnidades(unidadesData);
    };
    obtenerUnidades();
  }, []);

  useEffect(() => {
    const obtenerPilotos = async () => {
      const pilotosRef = collection(db, 'pilots');
      const pilotosSnap = await getDocs(pilotosRef);
      const pilotosData = pilotosSnap.docs.map((doc) => doc.data());
      const pilotosFiltrados = pilotosData.filter((piloto) => piloto.role === 'Piloto');
      setPilotos(pilotosFiltrados);
    };
    obtenerPilotos();
  }, []);

  useEffect(() => {
    const obtenerAuxiliares = async () => {
      const auxiliaresRef = collection(db, 'pilots');
      const auxiliaresSnap = await getDocs(auxiliaresRef);
      const auxiliaresData = auxiliaresSnap.docs.map((doc) => doc.data());
      const auxiliaresFiltrados = auxiliaresData.filter((auxiliar) => auxiliar.role === 'Auxiliar');
      setAuxiliares(auxiliaresFiltrados);
    };
    obtenerAuxiliares();
  }, []);

  useEffect(() => {
    const salidasRef = collection(db, 'salidaretorno');
    const unsubscribe = onSnapshot(salidasRef, (snapshot) => {
      const salidasData = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      const salidasFiltradas = salidasData.filter((salida) => salida.userId === userId && !salida.fechaRetorno);
      setSalidas(salidasFiltradas);
    });
    return unsubscribe;
  }, [userId]);

  useEffect(() => {
    const salidasRef = collection(db, 'salidaretorno');
    const unsubscribe = onSnapshot(salidasRef, (snapshot) => {
      const salidasData = snapshot.docs.map((doc) => ({ id: doc.id, ...doc.data() }));
      const salidasCompletasFiltradas = salidasData.filter((salida) => salida.userId === userId && salida.fechaRetorno);
      setSalidasCompletas(salidasCompletasFiltradas);
    });
    return unsubscribe;
  }, [userId]);

  const handleNumeroUnidadChange = (event) => {
    const numeroUnidad = event.target.value;
    setNumeroUnidad(numeroUnidad);
    const unidadSeleccionada = unidades.find((unidad) => unidad.number === numeroUnidad);
    if (unidadSeleccionada) {
      setPlaca(unidadSeleccionada.plate);
      setTipoUnidad(unidadSeleccionada.type);
    }
  };

  const handleKilometroSalidaChange = (event) => {
    setKilometroSalida(event.target.value);
  };

  const handleRutaChange = (event) => {
    setRuta(event.target.value);
  };

  const handleKilometroRetornoChange = (event) => {
    setKilometroRetorno(event.target.value);
  };

  const handlePilotoChange = (event) => {
    setPilotoSeleccionado(event.target.value);
  };

  const handleAuxiliarChange = (event) => {
    setAuxiliarSeleccionado(event.target.value);
  };

  const handleReportarSalida = async () => {
    if (numeroUnidad && placa && tipoUnidad && kilometroSalida && ruta && pilotoSeleccionado && auxiliarSeleccionado) {
      const salidasRef = collection(db, 'salidaretorno');
      const salidaData = {
        numeroUnidad,
        placa,
        tipoUnidad,
        kilometroSalida,
        ruta,
        piloto: pilotoSeleccionado,
        auxiliar: auxiliarSeleccionado,
        fechaSalida: new Date().toISOString().split('T')[0],
        horaSalida: new Date().toLocaleTimeString(),
        userId,
      };
      await addDoc(salidasRef, salidaData);
      setSalidas((prevSalidas) => {
        const nuevaSalida = {
          numeroUnidad,
          placa,
          tipoUnidad,
          kilometroSalida,
          ruta,
          piloto: pilotoSeleccionado,
          auxiliar: auxiliarSeleccionado,
          fechaSalida: new Date().toISOString().split('T')[0],
          horaSalida: new Date().toLocaleTimeString(),
          userId,
        };
        const existeSalida = prevSalidas.find((salida) => salida.numeroUnidad === nuevaSalida.numeroUnidad);
        if (!existeSalida) {
          return [...prevSalidas, nuevaSalida];
        } else {
          return prevSalidas;
        }
      });
      setNumeroUnidad('');
      setPlaca('');
      setTipoUnidad('');
      setKilometroSalida('');
      setRuta('');
      setPilotoSeleccionado('');
      setAuxiliarSeleccionado('');
    } else {
      alert('Todos los campos son obligatorios');
    }
  };

  const handleReportarRetorno = async (salida) => {
    if (salida && salida.id) {
      const salidasRef = doc(db, 'salidaretorno', salida.id);
      const salidaData = {
        fechaRetorno: new Date().toISOString().split('T')[0],
        horaRetorno: new Date().toLocaleTimeString(),
        kilometroRetorno,
      };
      await updateDoc(salidasRef, salidaData);
      setSalidas((prevSalidas) => {
        const index = prevSalidas.findIndex((salidaActual) => salidaActual.id === salida.id);
        if (index !== -1) {
          prevSalidas.splice(index, 1);
        }
        return prevSalidas;
      });
      setSalidasCompletas((prevSalidasCompletas) => {
        const nuevaSalidaCompleta = { ...salida, ...salidaData };
        return [...prevSalidasCompletas, nuevaSalidaCompleta];
      });
      setKilometroRetorno('');
    } else {
      console.error('No se ha obtenido el id de la salida');
    }
  };

  return (
    <div className="container mt-5">
      <h2>Reportar Salida</h2>
      <form>
        <div className="form-group">
          <label>Numero Unidad:</label>
          
          
          
          <select value={numeroUnidad} onChange={handleNumeroUnidadChange} className="form-control">
  <option value="">Seleccione una unidad</option>
  {unidades.sort((a, b) => a.number - b.number).map((unidad) => (
    <option key={unidad.number} value={unidad.number}>
      {unidad.number}
    </option>
  ))}
</select>



        </div>
        <div className="form-group">
          <label>Placa:</label>
          <input type="text" value={placa} readOnly className="form-control" />
        </div>
        <div className="form-group">
          <label>Tipo Unidad:</label>
          <input type="text" value={tipoUnidad} readOnly className="form-control" />
        </div>
        <div className="form-group">
          <label>Kilometro Salida:</label>
          <input type="number" value={kilometroSalida} onChange={handleKilometroSalidaChange} className="form-control" />
        </div>
        <div className="form-group">
          <label>Ruta:</label>
          <input type="text" value={ruta} onChange={handleRutaChange} className="form-control" />
        </div>
        <div className="form-group">
          <label>Piloto:</label>



          <select value={pilotoSeleccionado} onChange={handlePilotoChange} className="form-control">
  <option value="">Seleccione un piloto</option>
  {pilotos.sort((a, b) => a.name.localeCompare(b.name)).map((piloto) => (
    <option key={piloto.name} value={piloto.name}>
      {piloto.name}
    </option>
  ))}
</select>



        </div>
        <div className="form-group">
          <label>Auxiliar:</label>



          <select value={auxiliarSeleccionado} onChange={handleAuxiliarChange} className="form-control">
  <option value="">Seleccione un auxiliar</option>
  {auxiliares.sort((a, b) => a.name.localeCompare(b.name)).map((auxiliar) => (
    <option key={auxiliar.name} value={auxiliar.name}>
      {auxiliar.name}
    </option>
  ))}
</select>


        </div>
        <button type="button" onClick={handleReportarSalida} className="btn btn-primary">
          Reportar Salida
        </button>
      </form>
      <h2 className="mt-5">Ruta en proceso</h2>
      <div className="row">
  {salidas.map((salida, index) => (
    <div key={index} className="col-md-4">
      <div className="card mb-4">
        <div className="card-body">
          <h5 className="card-title">Salida {salida.numeroUnidad}</h5>
          <p className="card-text">
            Placa: {salida.placa}
            <br />
            Tipo Unidad: {salida.tipoUnidad}
            <br />
            Kilometro Salida: {salida.kilometroSalida}
            <br />
            Ruta: {salida.ruta}
            <br />
            Piloto: {salida.piloto}
            <br />
            Auxiliar: {salida.auxiliar}
            <br />
            Fecha Salida: {salida.fechaSalida}
            <br />
            Hora Salida: {salida.horaSalida}
          </p>
          <form>
            <div className="form-group">
              <label>Kilometro Retorno:</label>
              <input type="number" value={kilometroRetorno} onChange={handleKilometroRetornoChange} className="form-control" />
            </div>
            <button type="button" onClick={() => handleReportarRetorno({ id: salida.id, ...salida })} className="btn btn-primary">
              Reportar Retorno
            </button>
          </form>
        </div>
      </div>
    </div>
  ))}
</div>
      <h2 className="mt-5">Rutas Completadas</h2>
      <table className="table table-striped">
        <thead>
          <tr>
            <th>Numero Unidad</th>
            <th>Placa</th>
            <th>Tipo Unidad</th>
            <th>Kilometro Salida</th>
            <th>Ruta</th>
            <th>Piloto</th>
            <th>Auxiliar</th>
            <th>Fecha Salida</th>
            <th>Hora Salida</th>
            <th>Fecha Retorno</th>
            <th>Hora Retorno</th>
            <th>Kilometro Retorno</th>
          </tr>
        </thead>
        <tbody>
          {salidasCompletas.map((salida) => (
            <tr key={salida.id}>
              <td>{salida.numeroUnidad}</td>
              <td>{salida.placa}</td>
              <td>{salida.tipoUnidad}</td>
              <td>{salida.kilometroSalida}</td>
              <td>{salida.ruta}</td>
              <td>{salida.piloto}</td>
              <td>{salida.auxiliar}</td>
              <td>{salida.fechaSalida}</td>
              <td>{salida.horaSalida}</td>
              <td>{salida.fechaRetorno}</td>
              <td>{salida.horaRetorno}</td>
              <td>{salida.kilometroRetorno}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default RegistroSalidaRetorno;